
/* ============================== COMPONENTS ============================ */

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.about,
.features,
.premises,
.gallery,
.contacts {
    /* max-width: 820px; */
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

.border {
    height: 18px;
}


.border-about,
.border-gallery {
        padding-left: 0;
            padding-right: 0;
    background: linear-gradient(to right, #E7382F 50%, #00538F 50%);
}

.about-title-wrapper,
.gallery_title-wrapper {
    position: absolute;
    top: 0;
    right: 89px;
    left: 89px;
    /* max-width: 820px; */
    background: #00A08C;
    box-shadow: 3px 0px 4px 0px rgba(0, 0, 0, 0.25), -3px 0px 4px 0px rgba(0, 0, 0, 0.25);
}

.about-title,
.features_title,
.premises_title,
.gallery_title,
.contacts_title {
    padding-top: 11px;
    padding-bottom: 11px;
    text-align: center;
    color: #FFF;
    font-size: 22px;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
}


.border-features,
.border-premises,
.border-contacts {
    background: linear-gradient(to right, #00A08C 50%, #E7382F 50%)
}

.features_title-wrapper,
.premises_title-wrapper,
.contacts_title-wrapper {
    position: absolute;
    top: 0;
    right: 89px;
    left: 89px;
    max-width: 820px;
    background: #00538F;
    box-shadow: 3px 0px 4px 0px rgba(0, 0, 0, 0.25), -3px 0px 4px 0px rgba(0, 0, 0, 0.25);
}
/* ============================== /COMPONENTS ============================ */







