/* ============================== FOOTER  =============================== */

.footer {
    /* max-width: 820px; */
    background-color: #00538F;
    padding-top: 42px;
    padding-bottom: 142px;
}

.footer-container {
    max-width: 820px;
}

.footer-logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.logo {
    margin-left: auto;
    margin-right: auto;
    width: 220px;
    margin-bottom: 102px;
}

.footer-tel,
.footer-email {
    display: block;
    color: #FFF;
    font-size: 24px;
    font-weight: 400;
    line-height: normal;
}

.footer-tel:hover, .footer-tel:focus,
.footer-email:hover, .footer-email:focus {
    transform: scale(1.2);
}              

.footer-email {
    margin-top: 21px;
}



@media screen and (min-width: 821px) {
.footer {
        padding-bottom: 75px;
    }

    .footer-logo {
        max-width: 750px;
        display: flex;
        justify-content: space-between;
        margin-left: 90px;
        margin-right: 90px;
        margin-top: 52px;
        /* max-width: 1200px; */
    }

    .logo {
        margin-left: 0;
        margin-right: 0
    }

    .footer-contacts {
        display: block;
        text-align: right;
    }

    .footer-form-input {
        width: 264px;
        margin-bottom: 0;
    }

}


@media screen and (min-width: 821px) {
.footer-logo {
    max-width: 1210px;
}

}
/* ============================== /FOOTER  =============================== */